.box {
    --width: 9vmin;
    --height: 9vmin;
    --depth: 20vmin;
    /* width: var(--width); */
    height: var(--height);
    position: relative;
    /* background: hsla(var(--hue), 50%, 50%, var(--alpha)); */
    -webkit-perspective: 700000px;
            perspective: 700000px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateX(160deg) rotateY(19deg);
            transform: rotateX(160deg) rotateY(19deg);
    -webkit-animation: animateBox 5s infinite linear both;
            animation: animateBox 5s infinite linear both;
}

.box-face {
    position: absolute;
    left: 50%;
    top: 50%;
    /* background: hsla(var(--hue), 50%, 50%, var(--alpha)); */
    font-size: calc(1vw + 1rem);
    text-align: center;
    line-height: var(--height);
}

.front,
.back {
    width: var(--width);
    height: var(--height);
    --coeff: -0.5;
    -webkit-transform: translate(-50%, -50%) translate3d(0, 0, calc(var(--depth) * var(--coeff)));
            transform: translate(-50%, -50%) translate3d(0, 0, calc(var(--depth) * var(--coeff)));
    background: none;
}

.front {
    --coeff: 0.5;
}

.front img,
.back img {
    width: 100%;
    height: auto;
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
}

.left,
.right {
    width: var(--depth);
    height: var(--height);
    --rotate: 90deg;
    -webkit-transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5));
            transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5));
}

.left {
    --rotate: -90deg;
    border-bottom: 5px ridge black;
    -webkit-transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5)) translate(0px, 5px);
            transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5)) translate(0px, 5px);
    height: 8.5vmin;
    border-top: 5px ridge black;
}

.right {
    -webkit-transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5)) translateY(-4px) translateX(0px);
            transform: translate(-50%, -50%) rotateY(var(--rotate)) translate3d(0, 0, calc(var(--width) * 0.5)) translateY(-4px) translateX(0px);
    border: 5px ridge black;
    height: 6.5vmin;
    border-right: none;
    border-left: none;
}

.top,
.bottom {
    width: var(--width);
    height: var(--depth);
    --rotate: 90deg;
    -webkit-transform: translate(-50%, -50%) rotateX(var(--rotate)) translate3d(0, 0, calc(var(--height) * 0.5));
            transform: translate(-50%, -50%) rotateX(var(--rotate)) translate3d(0, 0, calc(var(--height) * 0.5));
}

.bottom {
    --rotate: -90deg;
    -webkit-transform: translate(-50%, -50%) rotateX(var(--rotate)) translate3d(0, 0, calc(var(--height) * 0.5)) translateX(-24px) translateY(1px);
            transform: translate(-50%, -50%) rotateX(var(--rotate)) translate3d(0, 0, calc(var(--height) * 0.5)) translateX(-24px) translateY(1px);
    border-right: 5px ridge black;
    width: 10vmin;
}


