@-webkit-keyframes animateBox {
    0% {
        -webkit-transform: rotateX(160deg) rotateY(0deg);
                transform: rotateX(160deg) rotateY(0deg);
    }
    100% {
        -webkit-transform: rotateX(160deg) rotateY(360deg);
                transform: rotateX(160deg) rotateY(360deg);
    }
}

@keyframes animateBox {
    0% {
        -webkit-transform: rotateX(160deg) rotateY(0deg);
                transform: rotateX(160deg) rotateY(0deg);
    }
    100% {
        -webkit-transform: rotateX(160deg) rotateY(360deg);
                transform: rotateX(160deg) rotateY(360deg);
    }
}
